@font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../../../Mont/AcciaSans-Regular.ttf")
}

@font-face {
    font-family: "Mont Easy";
    src:
        local("Mont"),
        url("../../../Mont/mont_extralightdemo.ttf")
}


.FramePPreview {
    flex-direction: column;
    min-width: 450px;
    align-self: flex-start;
    width: 450px;
    height: 600px;
    margin: 1%;
    position: relative;
    margin-top: 200px;

	display: block;
	/* width: 20%; */
	float: left;

}


.ImagePPreview {
    height: 600px;
    width: 450px;
    animation: fade-in 3s;
    position: relative;
	/* padding-bottom: 100%; */
	overflow: hidden;
}

.TextPPreview {
    margin: 2%;
    font-family: "Playfair Display";


    font-size: xx-large;
}

.Add-ButtonPPreview {
    height: 40px;
    width: 30%;
    font-family: "Playfair Display";

    font-size: x-large;

    display: grid;
    place-items: center;

}

.PricePPreview {
    margin: 2%;
    color: rgb(126, 126, 126);
    font-family: "Mont Easy";

    font-size: x-large;
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }

    20% {
        opacity: 50;
    }

    100% {
        opacity: 100;
    }
}
