
.MainFrameNB {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: normal;
    height: 320px;
    margin-bottom: 2%;
}

.frame-upNB {
    flex: 1;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 180px;
}

.frame-leftNB {
    flex: 1;
    display: flex;
    align-items: center;
}

.frame-rightNB {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.SearchNB {
    margin-right: 40px;
    margin-left: 10%;
    cursor: pointer;
}

.ShoppingCartNB {
    margin-right: 10%;
    cursor: pointer;
}

.frame-imageNB {
    /* flex: 2; */
    cursor: pointer;
    height: 220px;
    width: 220px;
}
