
@font-face {
    font-family: "Mont Easy";
    src:
      local("Mont"),
      url("../../../Mont/mont_extralightdemo.ttf")
  }
  
  @font-face {
    font-family: "Playfair Display";
    src:
      local("Mont"),
      url("../../../Mont/AcciaSans-Regular.ttf")
  }

.MainFrameBPM {
    flex: 9;
    display: flex;
    flex-direction: row;
    align-items: start;
    margin: 5%;
}

.ImageBPM {
    flex: 1;
    height: 180px;
    max-width: 136px;
    animation: fade-in 3s;
}

.Block-TextBPM {
    flex: 1;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 2%;

}

.Text-upBPM {
    flex: 1;
    display: flex;
    flex-direction: column;
}



.TextBPM {
    flex: 1;
    font-size: large;
    font-family: "Playfair Display";
    margin: 2%;
}

.DescriptionBPM {

    flex: 1;
    font-size: large;
    font-family: "Mont Easy";
    font-size: medium;
    margin: 2%;
    color: rgb(135, 138, 140);

}

.Text-downBPM {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  