@font-face {
    font-family: "Mont Easy";
    src:
        local("Mont"),
        url("../../Mont/mont_extralightdemo.ttf")
}


.MainFrameCB {
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
    /* width:  2500px; */

}


.frame-titleCB {
    flex: 1;
    margin: 1%;
    font-family: "Mont Easy";
    display: flex;
    text-align: center;
    cursor: pointer;
    font-size: large;
    align-items: center;
    justify-content: center;
}



@media only screen and (max-width: 1100px) {

    .frame-titleCB {
        height: 40px;
        font-size: small;
    }
}