@font-face {
    font-family: "Mont Easy";
    src:
        local("Mont"),
        url("../../../Mont/mont_extralightdemo.ttf")
}

@font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../../../Mont/AcciaSans-Regular.ttf")
}

.MainFramePPreviewM {
    display: flex;
    flex-direction: column;
    height: 350px;
    width: 45%;
    margin: 2.5%;
    cursor: pointer;
}

.ImagePPreviewM {
    height: 250px;
    width: 187.5px;
    animation: fade-in 3s;
}


.TextPPreviewM {
    font-family: "Playfair Display";
    margin: 2%;
}

.PricePPreviewM {
    font-family: "Mont Easy";
    margin: 2%;
}

.ButtonTextPPreviewM {

    font-family: "Playfair Display";
    width: 47.5%;
    margin: 1.25%;
    display: flex;
    align-items: center;
    border: 1mm solid #4c97d0;
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }

    20% {
        opacity: 50;
    }

    100% {
        opacity: 100;
    }
}