.Frame {
  flex: 1;
  display: flex;
  flex-direction: column;
}


.Frame-Image {
  flex: 1;
  display: flex;
  justify-content: center;
}

.Frame-ChangeImage {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Image {

  width: 450px;
  height: 600px;
  border-radius: 5%;

  animation: fade-in 3s;

}

.Frame-ChangeImage-Image {

  margin: 5%;
  width: 180px;
  height: 180px;
  border-radius: 5%;

}

.Frame-ChangeImage-Active {

  margin: 5%;
  width: 180px;
  height: 180px;
  border: 1px solid #000000;
  border-radius: 5%;
}

