@font-face {
    font-family: "Mont Easy";
    src:
        local("Mont"),
        url("../../../Mont/mont_extralightdemo.ttf")
}

@font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../../../Mont/AcciaSans-Regular.ttf")
}



.TextSFM {
    margin: 2%;
    font-family: "Playfair Display";
    font-size: x-large;
}



.FrameSFM {
    display: flex;
    flex-direction: row;
}

.ImageSFM {
    width: 60px;
    height: auto;
}

.Frame-ItemSFM {
    margin: 2%;
    border-radius: 5%;
    cursor: pointer;
}