.Desktop-ViewPage-Main-Frame {

    display: flex;
    flex-direction: column;
    height: 100vh;
}
@font-face {
    font-family: "Playfair Display";
    src:
      local("Mont"),
     url("../Mont/AcciaSans-Regular.ttf")
  }

@font-face {
    font-family: "Mont Easy";
    src:
      local("Mont"),
     url("../Mont/mont_extralightdemo.ttf")
  }
  

.Desktop-ViewPage-Interactive-Frame {
    flex: 7;
    display: flex;
}

.Desktop-ViewPage-Interactive-Content {
    flex: 1;
    flex-direction: column;
    display: flex;
}

.Desktop-ViewPage-Interactive-Up {
    margin-top: 100px;
    flex: 6;
    display: flex;
    flex-direction: row;
}

.Desktop-ViewPage-Interactive-Images {
    flex: 3;
    /* background-color: aqua; */
    display: flex;

}

.Desktop-ViewPage-Interactive-Text {
    flex: 3;
    font-family: "Playfair Display";


    padding: 80px;
    /* background-color: rgb(8, 0, 255); */

}

.Desktop-ViewPage-Interactive-Down {
    flex: 1;
    background-color: rgb(139, 12, 242);

}

.Desktop-ViewPage-ViewImagesDesktop {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.ViewImagesDesktop-MainImage {
    flex: 1;
    /* background-color: rgb(12, 242, 35); */
    display: flex;
    justify-content: center;
}

.ViewImagesDesktop-ChangeImage {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ViewImagesDesktop-MainImage-Image {

    width: auto;
    height: 550px;
    border-radius: 5%;

    animation: fade-in 3s;

}

.ViewImagesDesktop-ChangeImage-Image {

    margin: 5%;
    width: 180px;
    height: 180px;
    border-radius: 5%;

}

.ViewImagesDesktop-ChangeImage-Image-Active {

    margin: 5%;
    width: 180px;
    height: 180px;
    border: 1px solid #000000;
    border-radius: 5%;
}


@keyframes fade-in {
    0% {
        opacity: 0;
    }

    20% {
        opacity: 50;
    }

    100% {
        opacity: 100;
    }
}


.Text-Title {
    font-size: xx-large;
    margin: 2%;
    font-family: "Playfair Display";


    margin-top: 0%;

}

.Text-Description {
    font-family: "Mont Easy";

    font-size: x-large;
    margin: 2%;
    margin-top: 5%;
    width: 70%;
}

.Text-Price {
    font-family: "Mont Easy";

    margin: 2%;
    margin-top: 5%;

    font-size: x-large;
    color: rgb(88, 88, 88);
}

.Button-Add {
    font-family: "Mont Easy";

    height: 50px;
    width: 20%;
    font-size: x-large;
}