.App {
  background-color: rgb(110, 110, 110);
  margin: 0;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}



.Drawer {
  flex: 1;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.change-language {
  flex: 1;
}

.Navigation-Drawer-Title {
  flex: 1;
  cursor: pointer;

}

.ViewProduct-Interactive {
  flex: 9;
  display: flex;
}

.ViewProduct-Frame {
  display: inline-flex;
  flex-direction: column;
  width: 95%;
  margin: 2.5%;
}

.ViewProduct-Image {
  height: 420px;
  max-width: auto;
  animation: fade-in 3s;
}

.ViewProduct-text {
  margin: 2%;
  font-size: xx-large;
}

.ViewProduct-price {
  margin: 2%;

  font-size: x-large;
}

.ViewProduct-Desc {
  margin: 2%;

  font-size: large;
}

.ViewProduct-Option-Form {
  display: flex;
  flex-direction: row;

}

.ViewProduct-Option-Form-Image {
  width: 60px;
  height: auto;

}

.ViewProduct-Option-Frame {
  margin: 2%;
  /* border: 3px solid rgb(7, 64, 104); */
  border-radius: 5%;
  cursor: pointer;

}
