.App {
    background-color: rgb(110, 110, 110);
    margin: 0;
}

@font-face {
    font-family: "Mont Easy";
    src:
        local("Mont"),
        url("../Mont/mont_extralightdemo.ttf")
}

@font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../Mont/AcciaSans-Regular.ttf")
}

body,
html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}


.Desktop-HomePage {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.Desktop-NavigationBar {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: normal;
    height: 320px;
    margin-bottom: 2%;
}

.Desktop-NavigationBar-up {
    flex: 1;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 180px;
}

.Desktop-NavigationBar-left {
    flex: 1;
    display: flex;
    align-items: center;
}

.Desktop-NavigationBar-right {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.Desktop-NavigationBar-Search {
    margin-right: 40px;
    margin-left: 10%;
    cursor: pointer;
}

.Desktop-NavigationBar-ShoppingCart {
    margin-right: 10%;
    cursor: pointer;
}

.Desktop-NavigationBar-image {
    /* flex: 2; */
    cursor: pointer;
    height: 220px;
    width: 220px;
}

.Desktop-HomePage-Frame {
    /* flex: 2; */
    display: flex;
    flex-direction: column;
}

.Desktop-HomePage-Portfolio {
    /* flex: 5; */
    height: 750px;
}

.Desktop-HomePage-Portfolio-preImage {
    display: flex;
    padding-left: 20%;
    padding-top: 50px;
    justify-content: space-around;

}

.Desktop-HomePage-Portfolio-image {

    width: 450px;
    height: 600px
}

.Desktop-HomePage-Portfolio-text {
    flex: 1;
    margin-left: 30%;

    font-size: xx-large;
    font-family: "Playfair Display";

}

.Desktop-HomePage-Portfolio-desc {
    flex: 1;
    width: 50%;
    height: 520px;
    margin-left: 30%;
    margin-top: 40px;
    color: rgb(92, 92, 92);
    font-size: x-large;
    font-family: "Mont Easy";
    overflow: hidden
}

.Desktop-CategoryBar-Title {
    flex: 1;
    margin: 1%;
    font-family: "Mont Easy";
    display: flex;
    text-align: center;
    cursor: pointer;
    /* height: 80px; */
    font-size: large;
    align-items: center;
    justify-content: center;
}

.Desktop-HomePage-Four-Buttons {
    display: flex;
    flex-wrap: wrap;
}

.Desktop-HomePage-Four-Buttons-Text {
    height: 80px;
    width: 20%;
    margin: 2.5%;
    margin-top: 1%;
    font-family: "Playfair Display";

    font-size: xx-large;
    

	display: grid;
	place-items: center;
    border: 2mm solid #0a5fa1;
    /* border: 1mm solid #4c97d0; */



}


.Desktop-HomePage-Products {
    flex: 4;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: start;
}

.Desktop-ProductPreview-Frame {
    /* display: flex; */
    flex-direction: column;
    width: 20%;
    min-width: 450px;
    align-self: flex-start;
    height: 600px;
    margin: 2.5%;
    margin-top: 200px;
}

@media only screen and (max-width: 2100px) {
    .Desktop-HomePage-Four-Buttons-Text {
        font-size: x-large;
    }
}

@media only screen and (max-width: 1800px) {
    .Desktop-HomePage-Four-Buttons-Text {
        font-size: large;
    }
}


@media only screen and (max-width: 1500px) {

    .Desktop-HomePage-Portfolio-desc {
        font-size: medium;
    }

    .Desktop-HomePage-Four-Buttons-Text {
        font-size: large;
    }

    .Desktop-ProductPreview-image {
        height: 466px;
        width: 350px;
    }
}

@media only screen and (max-width: 1100px) {

    .Desktop-HomePage-Portfolio-desc {
        font-size: medium;
    }

    .Desktop-ProductPreview-Frame {
        width: 300px;
        height: 400px;
        /* margin: auto; */

    }

    .Desktop-HomePage-Four-Buttons-Text {
        font-size: small;
    }

    .Desktop-CategoryBar-Title {
        flex: 1;
        /* margin: 0%; */
        font-family: "Mont Easy";
        display: flex;
        text-align: center;
        cursor: pointer;
        height: 40px;
        font-size: small;
        align-items: center;
        justify-content: center;
    }
}

.Desktop-ProductPreview-image {
    height: 600px;
    width: 450px;
    animation: fade-in 3s;
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }

    20% {
        opacity: 50;
    }

    100% {
        opacity: 100;
    }
}

.Desktop-ProductPreview-text {
    margin: 2%;
    font-family: "Playfair Display";


    font-size: xx-large;
}

.Desktop-SearchBar {
    flex: 1;
    display: flex;

    flex-direction: column;
}


.Desktop-SearchBar-handler {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
}



.Desktop-change-language {
    flex: 1;
}

.Desktop-Navigation-Drawer-Title {
    flex: 1;
    font-size: large;
    font-family: "Playfair Display";


    cursor: pointer;

}

.Desktop-CategoryBar {
    display: flex;
    flex-direction: row;
    height: 2500px;

}

.Desktop-HomePage-Add-Button {
    height: 40px;
    width: 30%;
    font-family: "Playfair Display";

    font-size: x-large;

	display: grid;
	place-items: center;

}

.Desktop-ProductPreview-price {
    margin: 2%;
    color: rgb(126, 126, 126);
    font-family: "Mont Easy";

    font-size: x-large;
}

.Desktop-HomePage-NewIn {
    margin: 2%;
    margin-left: 5%;
    font-family: "Playfair Display";
    font-size: xx-large;
}