.Desktop-HomePage {
    display: flex;
    flex-direction: column;
    height: 100vh;
}


@font-face {
    font-family: "Mont Easy";
    src:
      local("Mont"),
     url("../Mont/mont_extralightdemo.ttf")
  }

@font-face {
    font-family: "Playfair Display";
    src:
      local("Mont"),
     url("../Mont/AcciaSans-Regular.ttf")
  }
  
.Basket-Interactive {
    flex: 8;
    display: flex;
}

.Basket-Products {
    flex: 1;
    display: flex;
}

.Basket-Product-Block {
    flex: 3;
}


.Basket-Quantity-Block {
    flex: 1;
}

.Basket-Price-Block {
    flex: 1;
    background-color: rgb(215, 250, 239);
}

.BasketPreview-Frame {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-left: 10%;
    margin-right: 8%;

    margin-top: 3%;
    height: 200px;
}

.BasketPreview-PreFrame {

    height: 150px;
    margin-bottom: 6%;
}

.BasketPreview-Frame-Main {
    display: flex;
    flex-direction: row;

    margin: 10%;
    height: 200px;
    width: 400px;
}

.BasketPreview-Frame-Quantity {
    display: flex;
    margin: 10%;
    /* margin-top: 150px; */
    flex-direction: row;
    align-items: center;
    height: 50px;
}

.BasketPreview-BlockText {
    margin-left: 5%;
    display: flex;
    flex-direction: column;
}

.BasketPreview-Name {
    font-family: "Playfair Display";

    font-size: x-large;
}

.BasketPreview-Image {
    width: 150px;
    border-radius: 5%;
}

.BasketPreview-Description {
    margin-top: 10%;
    font-family: "Mont Easy";

    font-size: x-large;

    color: rgb(84, 84, 84);
}

.BasketPreview-Price-Total {
    font-family: "Mont Easy";

    font-size: xx-large;
}