@font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../../Mont/AcciaSans-Regular.ttf")
}

@font-face {
    font-family: "Mont Easy";
    src:
        local("Mont"),
        url("../../Mont/mont_extralightdemo.ttf")
}

.MainFrameSPM {
    display: inline-flex;
    flex-direction: column;
    width: 45%;
    margin: 2.5%;
    cursor: pointer;
}

.ImageSPM {

    height: 220px;
    max-width: auto;
    animation: fade-in 3s;
}

.TextSPM {
    font-family: "Playfair Display";
    margin: 2%;
}

.PriceSPM {
    font-family: "Mont Easy";
    margin: 2%;
}

.ButtonTextSPM{
    font-family: "Playfair Display";
    width: 47.5%;
    margin: 1.25%;
	display: flex;
    align-items: center;
    border: 1mm solid #4c97d0;
}