@font-face {
    font-family: "Mont Easy";
    src:
      local("Mont"),
     url("../../../Mont/mont_extralightdemo.ttf")
  }

  @font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../../../Mont/AcciaSans-Regular.ttf")
}

.FramePP {
    height: 750px;
}

.PreImagePP {
    display: flex;
    padding-left: 20%;
    padding-top: 50px;
    justify-content: space-around;

}

.ImagePP {
    width: 450px;
    height: 600px
}

.TextPP {
    flex: 1;
    margin-left: 30%;
    font-size: xx-large;
    font-family: "Playfair Display";

}

.DescriptionPP {
    flex: 1;
    width: 50%;
    height: 520px;
    margin-left: 30%;
    margin-top: 40px;
    color: rgb(92, 92, 92);
    font-size: x-large;
    font-family: "Mont Easy";
    overflow: hidden
}