@font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../../../Mont/AcciaSans-Regular.ttf")
}

.FrameFB {
    display: flex;
    flex-wrap: wrap;
}


.Frame-Text {
    height: 80px;
    width: 20%;
    margin: 2.5%;
    margin-top: 1%;
    font-family: "Playfair Display";
    font-size: xx-large;
    display: grid;
    place-items: center;
    border: 2mm solid #0a5fa1;

}

@media only screen and (max-width: 2100px) {
    .Frame-Text {
        font-size: x-large;
    }
}

@media only screen and (max-width: 1800px) {
    .Frame-Text {
        font-size: large;
    }
}



@media only screen and (max-width: 1500px) {

    .Frame-Text {
        font-size: large;
    }

}

@media only screen and (max-width: 1100px) {

    .Frame-Text {
        font-size: small;
    }

}