.frameCQD {

    display: flex;
    margin: 2px;
    align-items: center;
    padding: 15px;
    justify-content: space-between;
    border: 1px solid black;
    border-radius: 10px;
    cursor: pointer;
}

.NumberCQD {
    flex: 1;
    margin: 5px;
    border: 0px;
    width: 100px;
    text-align: center;
    font-size: xx-large;
}

.PlusCQD {
    flex: 1;
    font-size: xx-large;
    cursor: pointer;
}

.MinusCQD {
    flex: 1;
    font-size: xx-large;
    cursor: pointer;

}