.App {
    background-color: rgb(110, 110, 110);
    margin: 0;
}

body,
html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}



@font-face {
    font-family: "Mont Easy";
    src:
        local("Mont"),
        url("../Mont/mont_extralightdemo.ttf")
}

@font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../Mont/AcciaSans-Regular.ttf")
}

.Mobile-HomePage {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.Mobile-NavigationBar {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Mobile-NavigationBar-left {
    flex: 1;
    display: flex;
    align-items: center;
}

.Mobile-NavigationBar-right {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.Mobile-NavigationBar-Catalog {
    flex: 2;
    cursor: pointer;
}

.Mobile-NavigationBar-Search {
    margin-right: 40px;
    cursor: pointer;
}

.Mobile-NavigationBar-ShoppingCart {
    margin-right: 40px;
    cursor: pointer;
}

.Mobile-NavigationBar-image {
    flex: 2;
    cursor: pointer;
    width: 10px;
}

.Mobile-HomePage-Frame {
    flex: 8;
    display: flex;
    flex-direction: column;
}

.Mobile-HomePage-Portfolio {
    flex: 3;
}

.Mobile-HomePage-Portfolio-preImage {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}


.Mobile-HomePage-Portfolio-image {
    width: 100%;
    height: 400px
}

.Mobile-HomePage-Four-Buttons {
    display: flex;
    flex-wrap: wrap;
}

.Mobile-HomePage-Four-Buttons-Text {
    font-family: "Playfair Display";
    width: 47.5%;
    margin: 1.25%;
	display: flex;
    align-items: center;
    border: 1mm solid #4c97d0;

}


.Mobile-HomePage-Products {
    flex: 4;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.Mobile-ProductPreview-Frame {
    display: inline-flex;
    flex-direction: column;
    width: 45%;
    margin: 2.5%;
    cursor: pointer;
}

.Mobile-ProductPreview-image {
    height: 220px;
    max-width: auto;
    animation: fade-in 3s;
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }

    20% {
        opacity: 50;
    }

    100% {
        opacity: 100;
    }
}

.Mobile-ProductPreview-text {
    font-family: "Playfair Display";

    margin: 2%;
}

.Mobile-ProductPreview-Price {

    font-family: "Mont Easy";


    margin: 2%;
}

.Mobile-SearchBar {
    flex: 1;
    display: flex;
    flex-direction: column;
}


.Mobile-SearchBar-handler {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
}

.Mobile-Drawer {
    flex: 1;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.Mobile-change-language {
    flex: 1;
}

.Mobile-Navigation-Drawer-Title {
    flex: 1;
    font-size: large;
    font-family: "Playfair Display";

    cursor: pointer;

}

.Frame-Sorted{
    font-family: "Playfair Display";
    /* margin: 2%; */
    margin-left: 2%;
    font-size: xx-large;
}

