@font-face {
    font-family: "Mont Easy";
    src:
        local("Mont"),
        url("../Mont/mont_extralightdemo.ttf")
}

@font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../Mont/AcciaSans-Regular.ttf")
}



.Mobile-ViewProduct-Interactive {
    flex: 9;
    display: flex;
}

.Mobile-ViewProduct-Frame {
    display: inline-flex;
    flex-direction: column;
    width: 95%;
    margin: 2.5%;
}

.Mobile-ViewProduct-Image {
    height: 420px;
    max-width: auto;
    animation: fade-in 3s;
}

.Mobile-ViewProduct-text {
    margin: 2%;
    font-family: "Playfair Display";

    font-size: xx-large;
}

.Mobile-ViewProduct-price {
    margin: 2%;
    font-family: "Mont Easy";

    font-size: x-large;
}

.Mobile-ViewProduct-text-option {

    margin: 2%;
    font-family: "Playfair Display";


    font-size: x-large;
}

.Mobile-ViewProduct-Desc {
    margin: 2%;
    font-family: "Mont Easy";

    font-size: large;
}

.Mobile-ViewProduct-Option-Form {
    display: flex;
    flex-direction: row;
}

.Mobile-ViewProduct-Option-Form-Image {
    width: 60px;
    height: auto;
}

.Mobile-ViewProduct-Option-Frame {
    margin: 2%;
    border-radius: 5%;
    cursor: pointer;
}