.App {
  background-color: rgb(110, 110, 110);
  margin: 0;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

@font-face {
  font-family: "Mont Easy";
  src:
    local("Mont"),
    url("../Mont/mont_extralightdemo.ttf")
}

@font-face {
  font-family: "Playfair Display";
  src:
    local("Mont"),
    url("../Mont/AcciaSans-Regular.ttf")
}

.Mobile-BasketPage {

  display: flex;
  flex-direction: column;
  height: 100vh;
}

.Mobile-BasketPreview-Frame {
  flex: 9;
  display: flex;
  flex-direction: row;
  align-items: start;
  margin: 5%;
}

.Mobile-BasketPreview-image {
  flex: 1;
  height: 180px;
  max-width: 140px;
  animation: fade-in 3s;
}

.Mobile-BasketPreview-Block-text {
  flex: 1;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 2%;

}

.Mobile-BasketPreview-text {
  flex: 1;
  font-size: large;
  font-family: "Playfair Display";
  margin: 2%;
  /* margin-left: 5%; */
}

.Mobile-BasketPreview-Title{

  flex: 1;
  font-size: large;
  font-family: "Playfair Display";
  margin: 2%;
  margin-left: 5%;
}

.Mobile-BasketPreview-text-desc {
  flex: 1;
  font-size: large;
  font-family: "Mont Easy";
font-size: medium;
  margin: 2%;
  color: rgb(135, 138, 140);
}


.Mobile-Basket-handler {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.Mobile-clear-Basket {
  flex: 1;
}

.Mobile-BasketPreview-text-down {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Mobile-BasketPreview-text-up {
  flex: 1;
  display: flex;
  flex-direction: column;
}



.Mobile-Basket-Frame {
  flex: 9;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
}

.Mobile-Basket-Frame-Empty {
  flex: 9;
  /* background-color: antiquewhite; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}