@font-face {
    font-family: "Mont Easy";
    src:
        local("Mont"),
        url("../../../Mont/mont_extralightdemo.ttf")
}

@font-face {
    font-family: "Playfair Display";
    src:
        local("Mont"),
        url("../../../Mont/AcciaSans-Regular.ttf")
}

.Frame-MainBPI {
    display: flex;
    flex-direction: row;

    margin: 10%;
    height: 200px;
    width: 400px;
}

.BlockTextBPI {
    margin-left: 5%;
    display: flex;
    flex-direction: column;
}

.NameBPI {
    font-family: "Playfair Display";

    font-size: x-large;
}

.ImageBPI {
    width: 150px;
    border-radius: 5%;
}

.DescriptionBPI {
    margin-top: 10%;
    font-family: "Mont Easy";

    font-size: x-large;

    color: rgb(84, 84, 84);
}